import { Component, OnInit } from '@angular/core';
import { TransporteService } from '../transporte.service';

@Component({
  selector: 'app-dashboards',
  templateUrl: './dashboards.component.html',
  styleUrls: ['./dashboards.component.css']
})
export class DashboardsComponent implements OnInit {

  public Gerencias;
  public Supervisoes;
  public condutores;
  
  public ranking1;
  public cols1;
  public ranking2;

  public gerencSelect;
  public supervSelect;
  public placSelect;

  public agendamentospendentes;
  public agendamentosaprovados;
  public agendamentosregistrados;


  constructor(private transporteService: TransporteService) { }

  ngOnInit() {


    
    
    this.Gerencias = [
      {label: '', value:0},
      {label: 'Comercial', value:1},
      {label: 'Operacional', value:2}
    ];
    this.Supervisoes = [
      {label: '', value:0},
      {label: 'Fiscalizacao', value:1},
      {label: 'Eletromecanica', value:2}
    ];
    this.condutores = [
      {label: '', value:0},
      {label: 'Albert Einstein', value:1},
      {label: 'Fred Mercury', value:2},
      {label: 'Jos Vincente', value: 3}
    ];

    this.cols1 = [
      {field: 'indice', header: '#  '},
      {field: 'placa', header: 'Placas'},
      {field: 'condutor', header: 'Condutores'},
      {field: 'nome', header: 'Responsveis Pelo Veculo'},
      {field: 'gerencia', header: 'Gerencias'},
      {field: 'departamento',  header: 'Departamento'}
    ];
    this.ranking1 = [
      {indice: 1, placa: 'HFE6699', condutor: 'NO IDENTIFICADO', nome:'Fulano de Tal', gerencia: 'Comercial', departamento: 'Fiscalizao'},
      {indice: 2, placa: 'HFE6710', condutor: 'Jos da Silva', nome:'Beltrano Silva', gerencia: 'Operacional gua', departamento: 'Eletromecnica'},
      {indice: 3, placa: 'HFE6711', condutor: 'NO IDENTIFICADO', nome:'Sicrano Oliveira', gerencia: 'Comercial', departamento: 'Comisso de Fraudes'},
      {indice: 4, placa: 'HFE6712', condutor: 'Weslley S.', nome:'Fred Mercury', gerencia: 'Planejamento', departamento: 'Planejamento'},
      {indice: 5, placa: 'HFE6713', condutor: 'Mahatma Gandhi', nome:'Mahatma Gandhi', gerencia: 'Operacional', departamento: 'Eletromecnica'},
      {indice: 6, placa: 'HFE6714', condutor: 'Antonio Jobim', nome:'Antonio Jobim', gerencia: 'Frotas', departamento: 'Frotas'},
      {indice: 7, placa: 'HFE6715', condutor: 'NO IDENTIFICADO', nome:'Larissa de Macedo', gerencia: 'Frotas', departamento: 'Frotas'},
      {indice: 8, placa: 'HFE6716', condutor: 'Luiz Gonzaga', nome:'Weslley S.', gerencia: 'Servios', departamento: 'Corte e Religao'},
      {indice: 9, placa: 'HFE6717', condutor: 'Jos da Silva', nome:'Jos da Silva', gerencia: 'Servios', departamento: 'Manuteno'},
      {indice: 10, placa: 'HFE6718', condutor: 'NO IDENTIFICADO', nome:'Jos das Couves', gerencia: 'Operacional gua', departamento: 'Automao'}
    ];


    this.transporteService.ParaAprovar()
    .subscribe(
    Agendamento  =>  {
      console.log(Agendamento)
      this.agendamentospendentes = Agendamento.length
    });

    this.transporteService.Aprovados()
    .subscribe(
    Agendamento  =>  {
      console.log(Agendamento)
      this.agendamentosregistrados = Agendamento.length
    });
    
    this.transporteService.Aprovados()
    .subscribe(
    Agendamento  =>  {
      console.log(Agendamento)
      this.agendamentosaprovados = Agendamento.filter(item => item.aprovacao===1).length
    });

  }

}
